import React from 'react';

const StatusTag = ({ status }) => {
  switch (status) {
    case 'IN_PROGRESS':
      return (
        <span
          className="tag is-small is-warning"
          style={{
            marginRight: 20,
          }}
        >
          In Progress
        </span>
      );
    case 'ARCHIVED':
      return (
        <span
          className="tag is-small is-warning"
          style={{
            marginRight: 20,
          }}
        >
          Archived
        </span>
      );
    default:
      return <span />;
  }
};

export default StatusTag;



